import { Logger } from '@gtLibTs/logger';
import { urlUtils } from '@gtLibTs/utils';
import { DetectDevice } from '@gtLibTs/utils/detectDevice';

import { UrlHelper } from '@gtLibTs/h5GameTools/urlHelper';

export enum TimeLabelKeys {
    entrance = 'entrance',
    loader = 'loader',
    ws = 'ws',
    login = 'login',
    takeMachine = 'takeMachine',
    loadInfo = 'loadInfo',
    setupCostumeStart = 'setupCostumeStart',
    setupCostumeEnd = 'setupCostumeEnd',
    machineDetailStart = 'machineDetailStart',
    machineDetailEnd = 'machineDetailEnd'
}

export type TimeLabels = { [key in TimeLabelKeys]?: TimeLabel } & { [key in string]?: TimeLabel };

export interface TimeLabel {
    label: TimeLabelKeys | string;
    preLabel?: TimeLabel;
    time: number;
    timeDiff: number;
    diffWith(label: TimeLabelKeys): number;
    diffWith(label: string): number;
}

export class LoadingInfo {
    static get shared() {
        if (window.gtLibTsScope?.loadingInfo) {
            return window.gtLibTsScope.loadingInfo;
        }
        return (window.gtLibTsScope ??= {}).loadingInfo = new LoadingInfo();
    }

    private timeLabels = {} as TimeLabels;
    private currentLabel: TimeLabelKeys | string = '';
    private firstTime = 0;
    public gameType = '';
    public userID = 0;

    public push(label: TimeLabelKeys): void;
    public push(label: string): void;
    public push(label: TimeLabelKeys | string) {
        if (this.timeLabels[label]) return;
        const self = this;

        const currentItem: TimeLabel = {
            label,
            preLabel: undefined,
            time: Date.now(),
            timeDiff: 0,
            diffWith(label) {
                const targetLabel = self.timeLabels[label];
                return this.time - (targetLabel?.time ?? 0);
            }
        };

        const prevItem = this.timeLabels[this.currentLabel];
        if (prevItem) {
            const prevTime = prevItem.time;
            currentItem.timeDiff = currentItem.time - prevTime;
            currentItem.preLabel = prevItem;
        } else {
            this.firstTime = currentItem.time;
        }

        this.currentLabel = label;
        this.timeLabels[label] = currentItem;
    }

    public show() {
        let showString = '';
        let currentItem = this.timeLabels[this.currentLabel];

        while(currentItem) {
            const date = new Date(currentItem.time);
            const timeFormat = `${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}.${date.getUTCMilliseconds()}`;
            showString = `${currentItem.preLabel?.label} => ${currentItem.label}: \n  time: ${timeFormat}\n  diff: ${currentItem.timeDiff}\n${showString}`;
            currentItem = currentItem.preLabel;
        }

        console.log(showString);
    }

    public totalTime() {
        const currentItem = this.timeLabels[this.currentLabel];
        if (!currentItem) return 0;

        return currentItem.time - this.firstTime;
    }

    public getTime(label: TimeLabelKeys): number;
    public getTime(label: string): number;
    public getTime(label: TimeLabelKeys | string) {
        return this.timeLabels[label]?.time ?? 0;
    }

    public sendLoadingTime() {
        if (
            !this.timeLabels.loader ||
            !this.timeLabels.ws ||
            !this.timeLabels.login ||
            !this.timeLabels.takeMachine ||
            !this.timeLabels.loadInfo ||
            !this.timeLabels.setupCostumeEnd
        ) return;

        const deviceInfo = DetectDevice.getDeviceInfo();
        const portal = (() => {
            const platform = urlUtils.getUrlQuery('platform');

            switch(platform) {
                case 'AIO':
                    return deviceInfo.mua || 'AIO';
                case 'app':
                    return 'APP';
                case '':
                    return DetectDevice.isPC ? 'PC' : 'PHONE';
                default:
                    return '其他';
            }
        })();

        const clickGame: number = (parent as any).playtime ?? this.getTime(TimeLabelKeys.entrance);
        const rd1Domain: string = (parent as any).memberDomain ?? '';
        const cdnDomain: string = (parent as any).CDNIP ?? location.host;
        const time_getMachineDetail = this.timeLabels.machineDetailEnd?.diffWith(TimeLabelKeys.machineDetailStart) ?? 0;

        const data = {
            GameType: UrlHelper.shared.gameType,
            HallID: UrlHelper.shared.hallId,
            UserID: this.userID,
            SessionID: UrlHelper.shared.sid,
            Domain: location.host,
            Browser: deviceInfo.pf || '',
            OS: deviceInfo.os || '',
            Portal: portal,
            CDN: cdnDomain,
            MemberDomain: rd1Domain,

            Time_Index: this.getTime(TimeLabelKeys.entrance) - clickGame,
            Time_LoadJS: this.timeLabels.loader.diffWith(TimeLabelKeys.entrance),
            Time_ConnectWebsocket: this.timeLabels.ws.diffWith(TimeLabelKeys.loader),
            Time_LoginCheck: this.timeLabels.login.diffWith(TimeLabelKeys.ws),
            Time_TakeMachine: this.timeLabels.takeMachine.diffWith(TimeLabelKeys.login),
            Time_OnLoadInfo2: this.timeLabels.loadInfo.diffWith(TimeLabelKeys.takeMachine),
            Time_GetMachineDetail: time_getMachineDetail,
            Time_SetupCostume: this.timeLabels.setupCostumeEnd.diffWith(TimeLabelKeys.setupCostumeStart),
            Time_Complete: (() => {
                const endTime = Math.max(this.getTime(TimeLabelKeys.setupCostumeEnd), this.getTime(TimeLabelKeys.machineDetailEnd));
                return endTime - this.getTime(TimeLabelKeys.loadInfo) -time_getMachineDetail;
            })()
        };

        window.gtag?.('event', 'game_load_time', {
            GameType: data.GameType,
            HallID: data.HallID,
            UserID: data.UserID,
            SessionID: data.SessionID,
            Domain: data.Domain,
            Browser: data.Browser,
            OS: data.OS,
            Portal: data.Portal,
            CDN: data.CDN,
            MemberDomain: data.MemberDomain,

            Time_LoadJS: data.Time_LoadJS,
            Time_ConnectWebsocket: data.Time_ConnectWebsocket,
            Time_LoginCheck: data.Time_LoginCheck,
            Time_TakeMachine: data.Time_TakeMachine,
            Time_OnLoadInfo2: data.Time_OnLoadInfo2,
            Time_GetMachineDetail: data.Time_GetMachineDetail,
            Time_SetupCostume: data.Time_SetupCostume
        });

        const result = {
            Index: data.Time_Index,
            Loader: data.Time_LoadJS,
            WebSocket: data.Time_ConnectWebsocket,
            LoginCheck: data.Time_LoginCheck,
            TakeMachine: data.Time_TakeMachine,
            OnLoadInfo2: data.Time_OnLoadInfo2,
            GetMachineDetail: data.Time_GetMachineDetail,
            Complete: data.Time_Complete
        };

        const sendData = new URLSearchParams();
        Object.entries(result).forEach(([key, value]) => {
            sendData.append(key, `${value}`);
        });

        fetch(
            `${UrlHelper.shared.domain}/ipl/portal.php/game/casinofrontend_entrance/loadingtime`,
            {
                method: 'POST',
                headers: {
                    'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8'
                },
                body: sendData
            }
        )
            .catch(() => {
                Logger.warn('發送loadingtime失敗');
            });
    }
}